.App {
  font-family: "Montserrat", "sans-serif" -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue";
  margin-top: 20px;
  text-align: center;
  max-width: 450px;
  height: 590px;
  margin-right: auto;
  margin-left: auto;
  background: #4281ee;
  border-radius: 10px;
  color: white;
  border-radius: 40px;
  background: linear-gradient(145deg, #478aff, #3b74d6);
  opacity: 0;
  animation: 2s ease-in appearFromNone forwards;
}

.location-info {
  opacity: 0;
  animation: 2s ease-in-out appearFromNone forwards;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@keyframes appearFromNone {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
